






import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ChatService } from "@/x-vue/services/chat/chat.service";
import { ChatRoomService } from "@/x-vue/services/chat/chat.room.service";

import ChatMessageList from "@/x-vue/components/chat/ChatMessageList.vue";

@Component({
  components: {
    ChatMessageList,
  },
})
export default class ChatUserRoomView extends Vue {
  chat = ChatService.instance;

  mounted(): void {
    // console.log("ChatUserRoomView::ChatMessageList; ");
    this.enter();
  }

  async enter(): Promise<void> {
    const roomInfo: Record<string, unknown> = {};
    if (this.$route.query.id != null) {
      roomInfo["id"] = this.$route.query.id;
    } else if (this.$route.query.users) {
      roomInfo["users"] = (this.$route.query.users as string).split(",");
    } else {
      return console.log("missing id or users firebaseUid");
    }

    if (this.$route.query.hatch != null) {
      roomInfo["hatch"] = this.$route.query.hatch == "true" ? true : false;
    }

    try {
      ChatRoomService.instance.enter(roomInfo);
    } catch (e) {
      this.$app.error(e);
    }
  }
}
